import { h } from 'preact';
import style from './style.css';
import { useState } from 'preact/hooks';

// Components
import CsButton from "@cloudscape-design/components/button"
import CsTextarea from "@cloudscape-design/components/textarea";
import CsGrid from "@cloudscape-design/components/grid";

const Home = () => {
	
	const [text, setText] = useState(0);
	const [processedText, setProcessedText] = useState(0);

	const converter = (text) => {
		setText(text)
		let processed_text = text
			.split("\n")
			.filter(el => {return el!=''})
			.map(i => "('"+i+"')")
		setProcessedText(processed_text)
	}

	return(
		<div class={style.home}>
			
			<CsGrid
				gridDefinition={[{ colspan: 6 }, { colspan: 6 }]}
			>
				<CsTextarea
					key={0}
					onChange={({ detail }) => converter(detail.value)}
					value={text}
					placeholder="Insert here the values to convert"
					rows={20}
				/>
				<CsTextarea
					key={1}
					value={processedText}
					placeholder="Copy from here the output"
					readOnly
					rows={20}
				/>
			</CsGrid>

		</div>
	)
}

export default Home;
